.LadingPage_button__baKWY{
    position: relative;
    display: block;
    height: 77px;
    width: 300px;
    border-radius: 4px;
    /* text-transform: uppercase; */
    background-color: transparent;
    color: #fff;
    font-size: 18px;
    overflow: hidden;
    transition: all 500ms ease;
    border: 2px solid #ff3cac;
    margin-bottom: 40px;
    z-index: 0;
    font-family: 'Silkscreen', cursive;
    font-weight: 700;
    text-decoration: none;
    cursor: pointer;
}

.LadingPage_button__baKWY::before{
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: auto;
    background-color: #ff3cac;
    transition: all 500ms ease;
    z-index: -1;
}

.LadingPage_button__baKWY:hover{
    background-color: #ff3cac;
    box-shadow: 0 0 10px #ff3cac, 0 0 20px #ff3cac, 0 0 20px #ff3cac;
    color: #434343;
}
